.weather {
    width: 400px;
    border-radius: 6px;
    box-shadow: 10px -2px 20px 2px rgb(0 0 0 /30%);
    color: #fff;
    background-color: #333;
    margin: 20px auto 0 auto;
    padding: 0 20px 20px 20px;
}

.top,
.bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.city {
    font-weight: 600;
    font-size: 32px;
    line-height: 1;
    margin: 0;
}

.details {
    font-weight: 400;
    font-size: 22px;
    line-height: 1;
    margin: 0;
}

.weather-icon {
    max-width: 100%;  
    height: 150px;  
}

.temperature {
    font-weight: 600;
    font-size: 70px;
    width:  auto;
    letter-spacing: -5px;
    margin: 10px 0;
}

.details {
    width: 100%;
    padding-left: 50px;
}

.parameter-row{
    display: flex;
    justify-content: space-between;
}

.parameter-label{
    text-align: left;
    font-weight: 400;
    font-size: 16px;
}

.parameter-value{
    text-align: right;
    font-weight: 600;
    font-size: 16px;
}